				"html": "<p>When finally breaking open my Short Box, I went straight for Sloane’s <em>A Hollowing</em>.</p>\n<p>…</p>\n<p>Ok, so I actually ate the candy. But THEN, I read <em>A Hollowing</em>.</p>\n<img class=\"floatCenter\" style=\"max-height: 600px;\" src=\"https://s3.amazonaws.com/foggy1/a_hollowing.png\" />\n<p>I’ve been writing about Sloane’s work ever since I started taking writing about comics seriously and I don’t think that’s a coincidence. Sloane makes it easy to take comics seriously. Her work on <em>From Under Mountains</em> <a target='_blank' href=\"http://retconrasengan.blogspot.com/2016/01/hanharas-grasp-letting-world-tell-story.html\">remains some of my favorite work</a> in the past few years, and her Patreon is a consistent source of the same quality of work. </p>\n<p>There is a constant sense that Sloane is in charge of the layout, rather than the other way around. Her work in <em>FUM</em> is evidence of this, as are the snippets of projects on her Patreon. For Sloane, layouts are a means of juxtaposing not only story beats, but also colors and textures; and often that juxtaposing extends to the third dimension wherein the story takes on layers that often carry you between physical spaces. </p>\n<p>(Seriously, some of the best examples of this are on <a href=\"https://www.patreon.com/sloane\">her Patreon</a>, and I’m not going to post work from there even though I’m DYING to talk about some of those pages, so check them out or be on the lookout for new work.)</p>\n<p><em>A Hollowing</em> leans more on the metaphorical space of dream-like (mostly nightmarish) imagery to guide the reader. It is a tale of trauma, the ways in which trauma resurfaces, and the ways in which we must confront the things that hurt us to move forward and survive in some kind of imperfect union. The tale unfolds through the eyes of a young girl, the loss of her mother, and a new horse. </p>\n<p>Told entirely in black and white, with many more “conventional” layouts than one might find in her other work, Sloane instead leans heavily on ink itself as a presence on the page, with the horse being the best example. This adds to the dreamlike effect of the gray, inkless watercolors that demarcate memories, which <em>itself</em> adds to scenes where the grays and inks meet in a collision of present and past traumas for the main character. </p>\n<figure class='floatCenter'>\n<img class=\"floatCenter\" style=\"max-height: 600px;\" src=\"https://s3.amazonaws.com/foggy1/door_wipe.png\" />\n</figure>\n<p>Above, the main character catches her father doing something you would rather not catch your father doing, and then flees to her room where she falls asleep and wakes up in a dream. The somewhat abrupt shift from running away to being in bed (particularly as juxtaposed between seeing her father through two doorways in a way that appears unsettling) captures the anxiety of being a kid seeing something you shouldn’t: it’s a whirlwind until you’re somewhere safe, and even then you’re usually caught stuck with your heart beating fast, going over what happened. Sloane captures that latter moment in the penultimate panel before launching us into a nasty flashback. A solid page, but still more layers to parse.</p>\n<p>In what appears to be a pretty standard layout, with “only” black and white at her disposal and no layered panels, Sloane still pulls off her trick of using overlayed elements to bridge the space between panels, not just once, but <em>twice</em>.</p>\n<p>The sheepish “Beeps” that surround the final panel in which the main character finds herself in a car begin in the panel in which she drifts off to sleep, rendering a quick panel transition as being one that occurs much more gradually.</p>\n<p>And then there’s the ink swipe. A smudge almost like a tire track, running from her father’s bedroom door in the upper right of the page, to her waking-into-a-dream consciousness in the bottom left. The pronounced gutters of this story—this page being no exception—preclude any serious layering from occurring, so the light touch here is just enough to traverse the normal series of events without taking the reader completely out of the page. To wit, I didn’t really absorb this smudge until the second time I read the story.</p>\n<p>The implications are clear: though her nightmares unearth past trauma <em>for the reader</em>, it’s the <em>current trauma</em> of what she sees through her father’s door that does the unearthing <em>for her</em>. The ink swipe physically connects points A and B on the page through the physical doorways she uses to traverse the house; it also connects the two beats where she simply lays her head down to sleep, the means by which she travels from the waking world to somewhere much darker.</p>\n<p>Without the “Beeps” and without the ink swipe, this page serves its purpose. Yet, these things aren’t window dressing: they are real, substantial parts of the story that augment the narrative in ways that additional panels could not achieve. They add ambiance, they alter our perception of the page’s pace, and they help round out the menacing tone of what is occurring and how it connects to what is to come. It is worth noting that the real punctuation to the events of this sequence does come two pages later, on the page turn, so the heightened sense of anticipation plays.</p>\n<p>There’s far more to talk about as far as <em>A Hollowing</em> is concerned, as the story on its own merits critical attention. I, however, am stuck as usual, marveling at the fact that regardless of the tools, Sloane demonstrates the full narrative depth of the medium.</p>\n<p><a target=_blank href=\"https://twitter.com/AustinLanari/status/873991542649876480\"><em>I made a thread for my thoughts on the other Short Box titles</em></a></p>",
